import Container from "../Container";
import Flex from "../Flex";
import MarkdownHeader from "../MarkdownHeader";
// import NavigationFooter from "templates/../NavigationFooter";
import React from "react";
import StickyResponsiveSidebar from "../StickyResponsiveSidebar";
import TitleAndMetaTags from "../TitleAndMetaTags";
import findSectionForPath from "../../utils/findSectionForPath";
import toCommaSeparatedList from "../../utils/toCommaSeparatedList";
import { sharedStyles } from "../../scss/theme";
import createOgUrl from "../../utils/createOgUrl";

import type { Node } from "types";

type Props = {
  authors: Array<string>,
  createLink: Function, // TODO: Add better flow type once we Flow-type createLink
  date?: string,
  enableScrollSync?: boolean,
  ogDescription: string,
  location: Location,
  markdownRemark: Node,
  sectionList: Array<Object>, // TODO: Add better flow type once we have the Section component
  titlePostfix: string
};

const MarkdownPage = ({
  authors = [],
  createLink,
  date,
  enableScrollSync,
  ogDescription,
  location,
  markdownRemark,
  sectionList,
  titlePostfix = ""
}: Props) => {
  const hasAuthors = authors.length > 0;
  const titlePrefix = markdownRemark.frontmatter.title || "";

  return (
    <Flex
      direction="column"
      grow="1"
      shrink="0"
      halign="stretch"
      css={{
        width: "100%",
        flex: "1 0 auto",
        position: "relative",
        zIndex: 0
      }}
    >
      <TitleAndMetaTags
        ogDescription={ogDescription}
        ogUrl={createOgUrl(markdownRemark.frontmatter.path)}
        title={`${titlePrefix}${titlePostfix}`}
      />
      <div css={{ flex: "1 0 auto" }}>
        <Container>
          <div css={sharedStyles.articleLayout.container}>
            <Flex type="article" direction="column" grow="1" halign="stretch">
              <MarkdownHeader title={titlePrefix} />

              {(date || hasAuthors) && (
                <div css={{ marginTop: 15 }}>
                  {date}{" "}
                  {hasAuthors && (
                    <span>
                      by{" "}
                      {toCommaSeparatedList(authors, author => (
                        <a
                          css={sharedStyles.link}
                          href={author.frontmatter.url}
                          key={author.frontmatter.name}
                        >
                          {author.frontmatter.name}
                        </a>
                      ))}
                    </span>
                  )}
                </div>
              )}

              <div css={sharedStyles.articleLayout.content}>
                <div
                  css={[sharedStyles.markdown]}
                  dangerouslySetInnerHTML={{ __html: markdownRemark.html }}
                />
              </div>
            </Flex>
          </div>
        </Container>
      </div>

      {/* TODO Read prev/next from index map, not this way */}
      {/* {(markdownRemark.frontmatter.next || markdownRemark.frontmatter.prev) && (
        <NavigationFooter
          location={location}
          next={markdownRemark.frontmatter.next}
          prev={markdownRemark.frontmatter.prev}
        />
      )} */}
    </Flex>
  );
};

export default MarkdownPage;



